.SwitchButton {
  display: flex;
  font-family: monospace;
  align-items: center;
}
.SwitchButton_container {
  width: 40px;
  border: solid 2px;
  border-radius: 50px;
  position: relative;
  height: 20px;
  cursor: pointer;
  border-color: #FF0000;
  margin-right: 15px;
  opacity: 0.3;
}
.SwitchButton_handle {
  background-color: #FF0000;
  position: absolute;
  width: calc(50%);
  height: calc(100%);
  border-radius: 60px;
  transition: transform 0.4s ease;
  transition-property: transform, opacity, background-color;
}
.SwitchButton input {
  display: none;
}
.SwitchButton-on .SwitchButton_container {
  border-color: #7ebdb4;
  opacity: 0.8;
}
.SwitchButton-on .SwitchButton_handle {
  background-color: #7ebdb4;
  transform: translateX(100%);
}
