.IncidentBox {
  display: flex;
  align-items: center;
  border: solid 1px #f2f2f2;
  color: grey;
  text-align: center;
  margin-bottom: 15px;
  margin-right: 15px;
  flex-direction: column;
  border-radius: 5px;
  height: fit-content;
}
.IncidentBox_heavyload {
  position: relative;
  margin: 0;
  padding: 10px;
}
.IncidentBox_background {
  top: 0;
  left: 0;
  content: '';
  position: absolute;
  height: 100%;
  width: 100%;
  opacity: 0.1;
  transition: opacity 0.5s ease;
}
.IncidentBox_background-heavyload {
  background-color: #FF0000;
}
.IncidentBox_background-recovery {
  background-color: #008000;
}
.IncidentBox_recovery {
  position: relative;
  margin: 0;
  padding: 10px;
}
.IncidentBox-clickable {
  cursor: pointer;
}
.IncidentBox-selected {
  color: black;
  background: #F8F8F8;
}
.IncidentBox-selected .IncidentBox_background {
  opacity: 0.3;
}
.IncidentBox-colorblind .IncidentBox_background-heavyload {
  background-color: #1E88E5;
}
.IncidentBox-colorblind .IncidentBox_background-recovery {
  background-color: #FFC107;
}
