.CpuLoadGraph {
  max-width: 1024px;
  margin-left: auto;
  margin-right: auto;
}
.CpuLoadGraph_settingsTitle {
  font-family: monospace;
}
.CpuLoadGraph_settings {
  display: flex;
  flex-wrap: wrap;
}
.CpuLoadGraph_settings .SwitchButton {
  margin-right: 15px;
}
.CpuLoadGraph_currentAverage {
  font-size: 1.5em;
  margin-top: 10px;
  margin-bottom: 10px;
}
.CpuLoadGraph_currentAverageValue {
  border-bottom: solid;
}
.CpuLoadGraph_mainBoard {
  font-family: monospace;
  padding: 9px;
  color: grey;
  margin-top: 15px;
  margin-bottom: 25px;
}
.CpuLoadGraph_incidentList {
  display: flex;
  flex-direction: row;
}
