.SvgGraph {
  position: relative;
}
.SvgGraph_heavyLoadPeriod {
  fill: #FF0000;
}
.SvgGraph_recoveryPeriod {
  fill: #008000;
}
.SvgGraph_snapshot {
  stroke: white;
  fill: #7ebdb4;
  stroke-width: 1px;
  cursor: pointer;
  transition: fill, stroke-width 0.5s ease-in-out;
}
.SvgGraph_snapshot-focus {
  fill: black;
}
.SvgGraph_cpuLine {
  fill: none;
  stroke: #7ebdb4;
}
.SvgGraph_axis {
  font-family: monospace;
}
.SvgGraph_axis path {
  display: none;
}
.SvgGraph_pointer {
  fill: red;
  width: 3px;
}
.SvgGraph_tooltip {
  background-color: white;
  position: absolute;
  top: 0;
  left: 64px;
  border: solid 1px #f2f2f2;
  border-radius: 3px;
  font-family: monospace;
  text-align: center;
}
.SvgGraph_tooltipHour {
  font-weight: bold;
  padding: 4px;
  margin: 0;
  border-bottom: solid 1px #f2f2f2;
}
.SvgGraph_tooltipLoad {
  margin: 0;
  padding: 4px;
  border-top: solid 1px #f5f5f5;
  background-color: #7ebdb41a;
}
.SvgGraph_displayButtonsTitle {
  font-family: monospace;
}
.SvgGraph_displayButtons {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}
.SvgGraph_buttonSeparator {
  width: 1px;
  background-color: black;
}
.SvgGraph_axisName {
  font-family: monospace;
}
.SvgGraph_axisName-cpu {
  position: absolute;
  top: 43%;
  left: -5%;
  transform: rotate(-90deg);
}
.SvgGraph_axisName-timeline {
  text-align: center;
}
.SvgGraph-colorblind .SvgGraph_cpuLine {
  stroke: #0088FF;
}
.SvgGraph-colorblind .SvgGraph_heavyLoadPeriod {
  fill: #1E88E5;
}
.SvgGraph-colorblind .SvgGraph_recoveryPeriod {
  fill: #FFC107;
}
